  .card {
    width: 38%;
    background: #ffffff;
    margin: auto;
    margin-top: 8%;
    border-radius: 25px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    padding: 0px;
    padding-bottom: 20px;
    padding-right: 10px;
  }
  
  .card_top {
    background: #f6f6f6;
    text-align: center;
    padding: 30px;
    position: relative;
    margin-top: 0px;
    margin-right: -10px;
    border-top-left-radius: 30px;
    border-top-right-radius: 30px;
  }
  
  
  @media all and (min-width:0px) and (max-width:900px){
    .card {
      width: 70%;
      margin-top: 60px;
    }
  }